import React from "react";

import { Logo } from "../../../components/Login/Logo";
import { Footer } from "../../../components/Login/Footer";
import { Button } from "../../../components/Login/Button";

import styles from '../Login.module.scss';
import { animations } from 'react-animation';

class Page5 extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            hobbies: ['Playstation', 'Nintendo', 'Xbox', 'Steam', 'Riot Games', 'Roblo', 'Free Fire', 'League of Legends', 'Valorant', 'Legends of Runeterra', 'Minecraft', 'Spotify', 'Deezer', 'Ifood', 'Uber', 'Tinder', 'iTunes', 'Google Play', 'IfooNetflixd', 'DAZN'],
            isToggleOn: true
        };

        this.handleClick = this.handleClick.bind(this);
    }
    
    handleClick = (no) => {
        this.setState(prevState => ({
            isToggleOn: !prevState.isToggleOn
        }));
    }
    
    render() {
        const animationStyle = {
            animation: animations.bounceIn
        }

        const {selectedProperties} = this.state

        return (
            <div className={styles.login_page5}>
                <div className={styles.loginwrap}>
                    <Logo />
                    <div className={styles.logincontent}>
                        <div className={styles.interest} style={animationStyle}>
                            <dl>
                                <dt>
                                    Escolha seus interesses
                                </dt>
                                <dd>
                                    Nós gostaríamos de entender seus gostos para te
                                    entregar conteúdo e promoções exclusivas.
                                </dd>
                            </dl>
                            <div class={styles.interest_check_wrap}>
                                <div className={styles.row}>
                                    {
                                        this.state.hobbies.map((hobby, idx) =>
                                            <span onClick={() => this.handleClick(idx)} className={this.state.isToggleOn ? styles.active : ''}>{hobby} <i className="fa fa-times"></i></span>
                                        )
                                    }
                                   
                                </div>
                            </div>
                            <p>Não quero compartilhar meus gostos, pular esta parte</p>
                            <Button />
                        </div>
                    </div>
                    <Footer />       
                </div>
            </div>
        )
    }
}

export default Page5;