import React from "react";

import { Logo } from "../../../components/Login/Logo";
import { Footer } from "../../../components/Login/Footer";
import { Button } from "../../../components/Login/Button";
import styles from '../Login.module.scss';
import { getImg } from "../../../utils/Helper";

import IconButton from '@mui/material/IconButton';
import FilledInput from '@mui/material/FilledInput';
import InputAdornment from '@mui/material/InputAdornment';
import FormControl from '@mui/material/FormControl';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import styled from "styled-components";
import { animations } from 'react-animation'

export default function Page2() {
    const [values, setValues] = React.useState({
        password: '',
        showPassword: false,
    });
    
    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };

    const handleClickShowPassword = () => {
        setValues({
            ...values,
            showPassword: !values.showPassword,
        });
    };

    const handleMouseDownPassword = (event) => {
         event.preventDefault();
    };

    const animationStyle = {
        animation: animations.bounceIn
    }

    const LoginStyle = styled.div`
        .css-r47a1p-MuiFormControl-root {
            margin: 0;
            width: 100%;
            background-color: #0D3840;
            border-radius: 5px;
        }

        .css-1gctnaj-MuiInputBase-input-MuiFilledInput-input  {
            margin-top: 0;
            padding: 0;
        }

        .css-i4bv87-MuiSvgIcon-root {
           path {
                color: #111 !important;
           }
        }
    }`;
    
    
    return (
        <LoginStyle>
            <div className={styles.loginwrap}>
                <Logo />
                <div className={styles.logincontent}>
                    <div className={styles.loginformwrap} style={animationStyle}>
                        <form name="login_form" id="login_form" method="POST" action="">
                            <dl>
                                <dt>
                                    Acesse sua conta
                                </dt>
                                <dd>
                                    <span>Não tem uma conta?</span>
                                    <span>Cadastre-se Aqui</span>
                                </dd>
                            </dl>

                            <div className={styles.form_group}>
                                <label for="email">Email</label>
                                <input type="text" className={styles.input} id="email"/>
                            </div>
                            <div className={styles.form_group}>
                                <label for="pass">Senha</label>
                                <span><a href="#">Esqueceu a senha?</a></span>
                                <div className={styles.input_wrap}>
                                <FormControl sx={{ m: 1, width: '25ch' }} variant="filled">
                                    <FilledInput
                                        id="filled-adornment-password"
                                        type={values.showPassword ? 'text' : 'password'}
                                        value={values.password}
                                        onChange={handleChange('password')}
                                        endAdornment={
                                        <InputAdornment position="end">
                                            <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword}
                                            onMouseDown={handleMouseDownPassword}
                                            edge="end"
                                            >
                                            {values.showPassword ? <VisibilityOff /> : <Visibility />}
                                            </IconButton>
                                        </InputAdornment>
                                        }
                                    />
                                    </FormControl>
                                </div>
                            </div>
                            <Button/>
                        </form>
                    </div> 
                </div>
                <Footer />       
            </div>
        </LoginStyle>
    )
}
